
.container{
    position: relative;
    height: 100vh;
    width: 100%;
    background-image: linear-gradient(to right bottom,#0E185F,#2FA4FF,#0E185F);
    display: flex;
    justify-content: center;
    align-items: center;
}
.glass{
    position: inherit;
    border: 2px;
    border-style: solid;
    border-color: blue;
    border-top: none;
    border-left: none;
    border-radius: 10px 10px 10px 10px;
    width: 95%;
    height: 90vh;
    background: linear-gradient(to right top,rgba(255,255,255,0.3),rgba(0,255,171, 0.3));
    backdrop-filter: blur(2px);
    z-index: 2;  
    display: flex;
    justify-content: space-around;
    
}
.contents{
    position: absolute;
    left: 10%;
    top: 25%;
    border: 1px;
    border-style: solid;
    border-color: blue;
    border-top: none;
    border-left: none;
    border-radius: 0px 0px 30px 0px;
    padding: 10px;
    font-size: 30px;
}
.btn{
    padding:10px 10px;
    outline: none;
    color: #fff;
    background: transparent;
    border: 1px solid #fff;
    cursor: pointer;
    margin: 3px;
    font-size: 20px;
}
.buttons{
    
    outline: none;
    color: #fff;
    background: transparent;
    cursor: pointer;
    position: relative;
    height: fit-content;
    display: flex;
    top: 85%;
}
.profileImage>img{
    width: 400px;
    height: 400px;
    border-radius: 30px 30px 30px 30px;
    position: absolute;
    left: 65%;
    top: 15%;
}
.circle{
    position: absolute;
    border-radius: 50%;
    background-image: linear-gradient(to top left,rgba(39, 153, 219,0.8),rgba(39, 153, 219,0.8),rgba(39, 153, 219,0.8),rgba(192, 183, 248,0.8),rgba(192, 183, 248,0.8));
}
.circle1{
    
    width:300px;
    height:300px;
    left:50%;
    bottom:50%;
}
.circle2{
    left: 15%;
    bottom: 10%;
    width:250px;
    height: 250px;
}
.circle3{
    left: 90%;
    bottom: 10%;
    width:150px;
    height:150px;
}
.circle4{
    left: 5%;
    bottom: 90%;
    width:150px;
    height: 150px;
}

            /* *********Media Queries******** */

